import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/home/Home.vue'
const Introduction =  () => import('../views/introduction/Introduction')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/Introduction',
    name: 'Introduction',
    component: Introduction
  }
]

const router = new VueRouter({
  routes
})

export default router
